<style scoped>
.albums{margin: 1.2rem 0rem;}
</style>

<template>
   <div class="albums">
       <!-- <h2>{{$route.params.musicId}}</h2> -->
       <aplayer autoplay :list="songs" :showLrc="isLrc"
        :music="{
            title: '红花会毕冉 Jesson - JUST A KID',
            artist: 'Silent Siren',
            src: '/static/lrc/红花会毕冉 Jesson - JUST A KID.mp3',
            pic: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1544149066&di=6821a4b4552c1d56fce3969c5dc852ad&imgtype=jpg&er=1&src=http%3A%2F%2Fi0.hdslb.com%2Fbfs%2Fface%2F74c25d2a169972bf8c35c835a9896a0a9b0402a7.jpg',
            lrc: 'https://api.bzqll.com/music/netease/lrc?key=579621905&id=526307800'
        }"
        />
   </div> 
</template>

<script>
import Axios from 'axios'
import Aplayer from 'vue-aplayer'

export default {
    components: {
        Aplayer
    },
    data(){
        return {
            songs: [],
            isLrc: false,
        }
    },
    mounted(){
        let url = './static/data/musicdata.json'
        let list = []

        Axios.get(url).then(res => {
            list = res.data.musicData;
            list.forEach(tune => {
                this.songs.push({
                    title: tune.title,
                    artist: tune.author,
                    src: tune.src,
                    pic: tune.musicImgSrc,
                    lrc: tune.lrc
                });
            });
            this.isLrc = true; //显示歌词
            console.log(this.songs);
        }).catch();
    },
    props: ['showLrc'],
  
}
</script>



