<style scoped>
.list{ margin:1rem 0rem; display:flex; flex-wrap: wrap;height: 100%; }
.list li{width:50%;}
.list li img{width:100%;height:200px;}
</style>

<template>
   <div>
       <ul class="list">
           <li v-for="music in musicList" :key="music.id">
               <router-link :to="'/music/MusicAlbums/'+music.id">
                   <img :src="music.bg" />
                   
               </router-link>           
           </li>
       </ul>
   </div> 
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return {
            musicList: []
        }
    },
    mounted() {
       let url = "./static/data/musiclist.json"

       Axios.get(url).then(res => {
           this.musicList = res.data.albums;
           console.log(res, this.musicList);
       }).catch(); 
    }
}
</script>



