<template>
    <div>
       <ul class="header">
           <li>
               <router-link to="/movie/movieList" >小说</router-link>
               <router-link to="/movie/movieList" ><img  class="b1" src="/static/img/a1.jpg" ></router-link>
               <img class="b3" src="/static/img/a3.jpg">
               <input class="d1" type="text" value="华晨宇" id="input"/>
                              
           </li>
           <li>
               <router-link to="/music" >乐库</router-link>               
           </li>
           <li>
               <router-link to="/book" >唱</router-link>               
           </li>
           <li>
               <router-link to="/photo" >直播</router-link>
               <img class="b2" src="/static/img/a2.jpg">               
           </li>
       </ul>
    </div>
</template>

<script>
export default { 
  methods:{
    goHome(){
      this.$router.push('/')
    }
  },
  props: ['bg']
};
</script>

<style scoped>
#input{height:25px;border:none;background: none;color: #939295;text-align: center;}
.header {
  width: 100%;
  height: 1.2rem;
  background: #018EFF;
  display: flex;
  line-height: 1rem;
  justify-content: space-evenly;
  top: 0;
  position: fixed;
  z-index: 999;
  
}
.header input{color: #C0E3FF; height:5px;border:none;background: none;text-align: center;position: absolute;margin-top: -0.3rem;margin-left: 0.6rem;}
.header input:focus{outline: none;}

.header .b1{position: absolute; margin-top: -0.75rem; margin-left: -0.8rem;width: 30px;height: 30px;}
.header .b2{position: absolute; margin-top: -0.85rem; margin-left: 0.6rem; width: 30px;height: 30px;}
.header .b3{width: 260px;height: 27px;position: absolute;margin-top: -0.3rem;}
.header li a{ color:#525252; outline:none;}
.header li a.router-link-active{color: #fff;}   


.title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
}
</style>




